/*
#overview
SVGアイコンフォント

「img/icon」ディレクトリ内のSVGファイルを元に生成されたアイコンフォント。
タスクランナーの処理で自動的にフォントファイル+SCSSの生成と本ガイドへの記載が行われる。
「i.icon-ファイル名」または「.iconfont（タグ内にファイル名と同じ文字列を入れる）」で呼び出すことができる（後者はIE非対応）。
サンプルコードではCSS追記のフック用に「.icon」クラスも付けている。
*/

@font-face {
  font-family: iconfont;
  src: url("font/icon/iconfont.eot?084c5e459b2fd02ebb2c276f1fd69bb5?#iefix") format("embedded-opentype"),
url("font/icon/iconfont.woff2?084c5e459b2fd02ebb2c276f1fd69bb5") format("woff2"),
url("font/icon/iconfont.woff?084c5e459b2fd02ebb2c276f1fd69bb5") format("woff");
  font-display: swap;
}

.iconfont {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-family: iconfont !important;
  font-style: normal;
  font-weight: normal !important;
  font-variant: normal;
  text-decoration: none;
  text-transform: none;
}
i[class*="icon-"] {
  display: inline-block;
  line-height: 1;
  vertical-align: middle;
	&::before {
    @extend .iconfont;
    display: inline-block;
    vertical-align: bottom;
	}
}
