<i class="icon-allow" style="margin-left:2rem;font-size:5rem"></i>
<span class="iconfont" style="display: inline-block;line-height: 1;vertical-align: middle;margin-left:2rem;font-size:10rem">allow</span>
```
*/
$icon-allow: "\f101";
.icon-allow::before {
